import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import RadioButtonCheckedIcon from '@material-ui/icons/RadioButtonChecked';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';


const useStyles = makeStyles(theme => ({
  appBar: {
    backgroundColor: '#22BCD2',
    top: 'auto',
    bottom: 0,
  },

}));

export default function BottomAppBar(props) {
  const classes = useStyles();

  return (
    <React.Fragment>
      <AppBar position="fixed" className={classes.appBar}>
        <BottomNavigation
          showLabels
          className={classes.appBar}
        >
          <BottomNavigationAction label="Inici" icon={<RadioButtonCheckedIcon />} onClick={() => props.setMenu(0)} />
          <BottomNavigationAction label="Mapa" icon={<LocationOnIcon />} onClick={() => props.setMenu(1)} />
          <BottomNavigationAction label="Afegir" icon={<MoreHorizIcon />} onClick={() => props.setDialog(true)} />
        </BottomNavigation>

      </AppBar>
    </React.Fragment>
  );
}
